import { useCallback, useEffect } from 'react';
import { ethers } from 'ethers';
import { useContract, useManagerContract } from './useContract';
import { useUpdate } from './useUpdate';
import { getProposalState } from '../utils/voteUtils';
import { useWallet } from '../store/wallet-context';

export const useVoteWithdrawable = (pid) => {
  const { account } = useWallet();
  const managerContract = useManagerContract();
  const { updateByTimer } = useUpdate();
  const { state, read } = useContract(
    managerContract,
    'voteWithdrawable',
    null,
    ethers.constants.Zero,
  );

  useEffect(() => {
    const fetchData = () => {
      if (!managerContract || !account || pid === null || pid === undefined) {
        return;
      }
      read([pid, account]);
    };
    fetchData();
  }, [updateByTimer, read, managerContract, pid, account]);
  return state;
};

export const useGetProposalsLength = () => {
  const managerContract = useManagerContract();
  const { updateByTimer } = useUpdate();
  const { state, read } = useContract(
    managerContract,
    'getProposalsLength',
    null,
    ethers.constants.Zero,
  );
  useEffect(() => {
    const fetchData = () => {
      if (!managerContract) {
        return;
      }
      read();
    };
    fetchData();
  }, [updateByTimer, read, managerContract]);
  return state;
};

export const useGetProposals = () => {
  const decodeProposals = useCallback(
    (proposals) =>
      proposals.map((item, index) => ({
        ...item,
        pid: index,
        state: getProposalState(
          item.startTimestamp.toNumber() * 1000,
          item.endTimestamp.toNumber() * 1000,
        ),
      })),
    [],
  );

  const { data: proposalsLength } = useGetProposalsLength();
  const managerContract = useManagerContract();
  const { updateByTimer } = useUpdate();
  const { state, read } = useContract(managerContract, 'getProposals', decodeProposals, []);
  useEffect(() => {
    const fetchData = () => {
      if (!managerContract || proposalsLength.eq(ethers.constants.Zero)) {
        return;
      }
      read([ethers.constants.Zero, proposalsLength]);
    };
    fetchData();
  }, [updateByTimer, read, managerContract, proposalsLength]);
  return state;
};

export const useGetVote = (pid) => {
  const { account } = useWallet();
  const managerContract = useManagerContract();
  const { updateByTimer } = useUpdate();
  const { state, read } = useContract(managerContract, 'getVote', null, []);
  useEffect(() => {
    const fetchData = () => {
      if (!managerContract || pid === null || pid === undefined || !account) {
        return;
      }
      read([pid, account]);
    };
    fetchData();
  }, [updateByTimer, read, managerContract, pid, account]);
  return state;
};

export const useVote = () => {
  const managerContract = useManagerContract();

  const { state, write } = useContract(managerContract, 'vote');

  const vote = async (pid, optionIndex, zebecAmount) => {
    if (
      !(
        managerContract &&
        pid !== null &&
        optionIndex !== null &&
        zebecAmount &&
        zebecAmount.gt(ethers.constants.Zero)
      )
    ) {
      return;
    }
    await write([pid, optionIndex, zebecAmount]);
  };

  return { state, vote };
};

export const useVoteWithdraw = () => {
  const managerContract = useManagerContract();

  const { state, write } = useContract(managerContract, 'voteWithdraw');

  const voteWithdraw = async (pid) => {
    if (!(managerContract && pid !== null)) {
      return;
    }
    await write([pid]);
  };

  return { state, voteWithdraw };
};
