import React, { useMemo } from 'react';
import { ethers } from 'ethers';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import { CheckOutlined } from '@ant-design/icons';
import styles from './OptionItem.module.scss';
import { formatNumber } from '../../../utils/utils';

const OptionItem = (props) => {
  const { item, index, optionVotes, selectedOption, voterRecord, onClick } = props;

  const { voteMax } = voterRecord;

  // detect if the current option item is voted
  const voted = useMemo(() => {
    if (voteMax && voteMax[index].gt(0)) {
      return true;
    }
    return false;
  }, [index, voteMax]);

  // calculate vote percent for background

  const percent = useMemo(() => {
    try {
      const totalVote = optionVotes.reduce(
        (accum, votes) => accum.add(votes),
        ethers.constants.Zero,
      );
      const tempPercent = optionVotes[index].toNumber() / totalVote.toNumber();
      return Number.isNaN(tempPercent) ? 0 : tempPercent;
    } catch (error) {
      return 0;
    }
  }, [index, optionVotes]);

  return (
    <>
      {/* {isFinishedRecord.winIndex === index && (
        <img className={styles.voteResult} src="/static/images/icon/win.png" alt="win" />
      )} */}
      <div
        className={classnames(styles.OptionItem, { [styles.selected]: selectedOption === index })}
        onClick={() => {
          onClick(index);
        }}>
        <div className={classnames(styles.optionState, { [styles.voted]: voted })}>
          {voted && <CheckOutlined style={{ color: 'black', fontSize: '20px' }} />}
        </div>
        <div className={styles.textWrap}>
          <h2 className={styles.optionName}>{item}</h2>
          <div className={styles.valueWrap}>
            <p>{formatNumber(ethers.utils.formatUnits(optionVotes[index], 9))} ZBC</p>
            <p className={styles.percent}>{formatNumber(percent, '0.[00]%')}</p>
          </div>
          <div className={styles.percentBarOutter}>
            <div style={{ width: `${percent * 100}%` }} className={styles.percentBar} />
          </div>
        </div>
      </div>
    </>
  );
};

OptionItem.propTypes = {
  item: PropTypes.string.isRequired,
  index: PropTypes.number.isRequired,
  // eslint-disable-next-line react/forbid-prop-types
  voterRecord: PropTypes.arrayOf(PropTypes.any).isRequired,
  selectedOption: PropTypes.number.isRequired,
  // eslint-disable-next-line react/forbid-prop-types
  optionVotes: PropTypes.arrayOf(PropTypes.any).isRequired,
  onClick: PropTypes.func.isRequired,
};

OptionItem.defaultProps = {};
export default OptionItem;
