import React from 'react';
import PropTypes from 'prop-types';
import { List } from 'antd';
import { NavLink } from 'react-router-dom';
import classNames from 'classnames';
import { ethers } from 'ethers';

// import PercentStackedBar from '../PercentStackedBar/PercentStackedBar';
// import { truncAddress } from '../../../utils/utils';
import styles from './ProposalList.module.scss';
import { PROPOSAL_INFO, PROPOSAL_STATE } from '../../../utils/voteUtils';

const ProposalList = (props) => {
  const { items } = props;
  return (
    <div className={styles.ProposalList}>
      {!items || !Array.isArray(items) || items.length === 0 ? null : (
        <List
          dataSource={items}
          renderItem={(item) => (
            <List.Item>
              <NavLink className={styles.proposalItem} to={`/governance/detail/${item.pid}`}>
                <div className={styles.proposalContent}>
                  <div
                    className={classNames(styles.status, {
                      [styles.ended]: item.state === PROPOSAL_STATE.ENDED,
                    })}>
                    {item.state}
                  </div>
                  <h1>{item.topic}</h1>
                  <div className={styles.timeWrap}>
                    <p className={styles.infoItem}>
                      <span>Author:</span>
                      {PROPOSAL_INFO[item.pid]?.author}
                    </p>
                  </div>

                  <div className={styles.timeWrap}>
                    <p className={styles.infoItem}>
                      <span>Start Time:</span>
                      {new Date(item.startTimestamp.toNumber() * 1000).toUTCString().slice(5)}
                    </p>
                    <p className={styles.infoItem}>
                      <span>End Time:</span>
                      {new Date(item.endTimestamp.toNumber() * 1000).toUTCString().slice(5)}
                    </p>
                    {PROPOSAL_INFO[item.pid]?.quorum && (
                      <p className={styles.infoItem}>
                        <span>Quorum</span>
                        {item.optionVotes
                          .reduce((accum, votes) => accum.add(votes), ethers.constants.Zero)
                          .gte(PROPOSAL_INFO[item.pid]?.quorum) ? (
                          <img src="/static/images/Icons/check.svg" alt="pass" />
                        ) : (
                          <img src="/static/images/Icons/cross.svg" alt="not pass" />
                        )}
                      </p>
                    )}
                  </div>
                  {/* <div className={styles.percentBar}>
                    <PercentStackedBar options={item.options} optionVotes={item.optionVotes} />
                  </div> */}
                </div>
              </NavLink>
            </List.Item>
          )}
        />
      )}
    </div>
  );
};

ProposalList.propTypes = {
  // eslint-disable-next-line react/forbid-prop-types
  items: PropTypes.arrayOf(PropTypes.any),
};

ProposalList.defaultProps = {
  items: null,
};

export default ProposalList;
