import React, { useState } from 'react';
import { ethers } from 'ethers';
import { useTranslation } from 'react-i18next';
import { parseUnits } from 'ethers/lib/utils';
import { message, Spin } from 'antd';
import { NavLink, useParams } from 'react-router-dom';
import { LeftOutlined } from '@ant-design/icons';
import classNames from 'classnames';
import {
  useGetProposals,
  useGetVote,
  useVote,
  useVoteWithdraw,
  useVoteWithdrawable,
} from '../../hooks/useVote';
import MainContainer from '../../components/UI/MainContainer/MainContainer';
import CustomInput from '../../components/UI/CustomInput/CustomInput';
import styles from './VoteDetail.module.scss';
import { PROPOSAL_INFO, PROPOSAL_STATE } from '../../utils/voteUtils';
import OptionItem from '../../components/Vote/OptionItem/OptionItem';
import { formatNumber } from '../../utils/utils';
import CustomButton from '../../components/UI/CustomButton/CustomButton';
import CustomModal from '../../components/UI/CustomModal/CustomModal';
import { useAllTokenAllowance, useAllTokenBalance, useTokenApprove } from '../../hooks/useToken';
import { CONTRACT_NAME } from '../../constants/addresses';
import { useWallet } from '../../store/wallet-context';
import { useNameservice } from '../../store/nameservice-context';

const VoteDetail = () => {
  const { t } = useTranslation();
  const { account } = useWallet();
  const [selectedOption, setSelectedOption] = useState(-1);
  const [stakeModalVisible, setStateModalVisible] = useState(false);
  const [stakeInput, setStakeInput] = useState('');

  const { id } = useParams();
  const { data: proposals } = useGetProposals();
  const proposal = proposals?.[id];

  const { data: voterRecord } = useGetVote(id);
  const { data: voteWithdrawable } = useVoteWithdrawable(id);
  const allTokenBalance = useAllTokenBalance();
  const { data: zbcBalance } = allTokenBalance[CONTRACT_NAME.ZBC];
  const { data: nativeBalance } = allTokenBalance.nativeBalance;

  const { state: voteState, vote } = useVote();
  const { state: voteWithdrawState, voteWithdraw } = useVoteWithdraw();

  const addresses = useNameservice();
  // get balance and allowance
  const allTokenAllowance = useAllTokenAllowance(addresses[CONTRACT_NAME.Manager]);
  const { data: allowance } = allTokenAllowance[CONTRACT_NAME.ZBC];
  const { state: approveTokenState, approve } = useTokenApprove(addresses[CONTRACT_NAME.ZBC]);

  if (!proposal) {
    return (
      <div className={styles.loadingPlaceholder}>
        <Spin />
      </div>
    );
  }

  const options = proposal.options.slice(0, proposal.optionNumber.toNumber());
  const optionVotes = proposal.optionVotes.slice(0, proposal.optionNumber.toNumber());

  const votedIndex = voterRecord.voteMax?.findIndex((item) => item.gt(0)) ?? -1;
  const stakedZBC = voterRecord.voteBalance?.reduce(
    (accum, item) => accum.add(item),
    ethers.constants.Zero,
  );

  const DescComp = PROPOSAL_INFO[id]?.DescComp;

  const handleStakeInputChange = (e) => {
    const {
      target: { value },
    } = e;
    if (value.length === 0) {
      setStakeInput(value);
      return;
    }
    try {
      ethers.utils.parseUnits(value, 9);
      setStakeInput(value);
    } catch (error) {
      console.log('invalid input');
    }
  };

  const toggleStakeModal = () => {
    setStateModalVisible((prev) => !prev);
  };

  const handleVote = async () => {
    if (voteState.isLoading) {
      return;
    }
    if (!account) {
      message.info(t('Please connect to wallet', 'Please connect to wallet'));
      return;
    }
    if (proposal.state !== PROPOSAL_STATE.LIVE) {
      message.info('Please vote on a live proposal');
      return;
    }
    const amount = parseUnits(stakeInput, 9);
    if (amount.gt(parseUnits(zbcBalance.toString(), 9))) {
      message.info(
        t('Not enough currency', 'Not enough {{currency}}', { currency: CONTRACT_NAME.ZBC }),
      );
      return;
    }
    if (nativeBalance < 0.01) {
      message.info(
        t(
          "You don't have enough BNB for gas to complete the transaction",
          "You don't have enough BNB for gas to complete the transaction",
        ),
      );
      return;
    }
    if (votedIndex !== -1 && votedIndex !== selectedOption) {
      message.info('You can only vote for one option');
      return;
    }
    try {
      await vote(id, selectedOption, amount);
    } catch (e) {
      console.log('vote failed');
    }
  };

  const handleVoteWithdraw = async () => {
    if (voteWithdrawState.isLoading) {
      return;
    }
    if (!account) {
      message.info(t('Please connect to wallet', 'Please connect to wallet'));
      return;
    }
    if (proposal.state !== PROPOSAL_STATE.ENDED) {
      message.info('Please withdraw on a ended proposal');
      return;
    }
    if (!voteWithdrawable.gt(0)) {
      message.info('Nothing to withdraw');
      return;
    }
    if (nativeBalance < 0.01) {
      message.info(
        t(
          "You don't have enough BNB for gas to complete the transaction",
          "You don't have enough BNB for gas to complete the transaction",
        ),
      );
      return;
    }
    try {
      await voteWithdraw(id);
    } catch (e) {
      console.log('vote withdraw failed', e);
    }
  };
  const handleApprove = () => {
    if (approveTokenState.isLoading || !addresses[CONTRACT_NAME.Manager]) {
      return;
    }
    if (!account) {
      message.info(t('Please connect to wallet', 'Please connect to wallet'));
      return;
    }
    if (nativeBalance < 0.01) {
      message.info(
        t(
          "You don't have enough BNB for gas to complete the transaction",
          "You don't have enough BNB for gas to complete the transaction",
        ),
      );
      return;
    }
    // if (!checkTime(config.startTimestamp * 1000)) {
    //   message.info(t('Not start yet', 'Not start yet'));
    //   return;
    // }
    // if (!agreementChecked) {
    //   message.info(
    //     t(
    //       'Please check the box for the risk agreement',
    //       'Please check the box for the risk agreement',
    //     ),
    //   );
    //   return;
    // }
    approve(addresses[CONTRACT_NAME.Manager]);
  };

  return (
    <>
      <CustomModal
        open={stakeModalVisible}
        title="Stake Amount"
        showTitle
        onCancel={toggleStakeModal}>
        <div className={styles.stakeModal}>
          <div className={styles.balance}>
            <p>Balance</p>
            <p>{formatNumber(zbcBalance)} ZBC</p>
          </div>
          <CustomInput value={stakeInput} onChange={handleStakeInputChange} />
          {allowance ? (
            <CustomButton
              className={styles.voteButton}
              loading={voteState.isLoading}
              onClick={handleVote}>
              Stake
            </CustomButton>
          ) : (
            <CustomButton
              className={styles.voteButton}
              loading={approveTokenState.isLoading}
              onClick={handleApprove}>
              Approve ZBC
            </CustomButton>
          )}
        </div>
      </CustomModal>
      <div className={styles.VoteDetail}>
        <div className={styles.banner}>
          <MainContainer className={styles.bannerContainer}>
            <NavLink className={styles.backButton} to="/vote">
              <LeftOutlined />
              <span>Back</span>
            </NavLink>
          </MainContainer>
        </div>
        <MainContainer className={styles.mainContent}>
          <div className={styles.descWrap}>{!!DescComp && <DescComp />}</div>
          <div className={styles.actionWrap}>
            <div className={styles.actionCard}>
              <div className={styles.titleWrap}>
                <h2>Vote Results</h2>
                <div
                  className={classNames(styles.status, {
                    [styles.ended]: proposal.state === PROPOSAL_STATE.ENDED,
                  })}>
                  {proposal.state}
                </div>
              </div>
              <div className={styles.optionWrap}>
                {options.map((item, index) => (
                  <OptionItem
                    key={item}
                    item={item}
                    index={index}
                    optionVotes={optionVotes}
                    selectedOption={selectedOption}
                    voterRecord={voterRecord}
                    onClick={(option) => {
                      setSelectedOption(option);
                    }}
                  />
                ))}
              </div>
              <div className={styles.statsWrap}>
                <div className={styles.statsItem}>
                  <h2>Start Date</h2>
                  <p>
                    {new Date(proposal.startTimestamp.toNumber() * 1000).toUTCString().slice(5)}
                  </p>
                </div>
                <div className={styles.statsItem}>
                  <h2>End Date</h2>
                  <p>{new Date(proposal.endTimestamp.toNumber() * 1000).toUTCString().slice(5)}</p>
                </div>
                <div className={styles.statsItem}>
                  <h2>Quorum</h2>
                  {proposal.optionVotes
                    .reduce((accum, votes) => accum.add(votes), ethers.constants.Zero)
                    .gte(PROPOSAL_INFO[proposal.pid]?.quorum) ? (
                    <p>
                      Reached
                      <img src="/static/images/Icons/check.svg" alt="pass" />
                    </p>
                  ) : (
                    <p>
                      Not Reached
                      <img src="/static/images/Icons/cross.svg" alt="not pass" />
                    </p>
                  )}
                </div>
              </div>
            </div>
            <div className={styles.actionCard}>
              <div className={styles.titleWrap}>
                <h2>Your Vote</h2>
              </div>
              <div className={styles.statsWrap}>
                <div className={styles.statsItem}>
                  <h2>Voted</h2>
                  <p>{votedIndex === -1 ? '-' : options[votedIndex]}</p>
                </div>
                <div className={styles.statsItem}>
                  <h2>Staked</h2>
                  <p>
                    {formatNumber(
                      ethers.utils.formatUnits(stakedZBC || ethers.constants.Zero, 9),
                      '0,0.[00]',
                    )}{' '}
                    ZBC
                  </p>
                </div>
              </div>
              {proposal.state === PROPOSAL_STATE.UPCOMING && (
                <CustomButton className={styles.voteButton} secondary disabled>
                  {PROPOSAL_STATE.UPCOMING}
                </CustomButton>
              )}
              {proposal.state === PROPOSAL_STATE.LIVE && (
                <CustomButton
                  className={styles.voteButton}
                  secondary
                  onClick={toggleStakeModal}
                  loading={voteState.isLoading}>
                  Vote
                </CustomButton>
              )}
              {proposal.state === PROPOSAL_STATE.ENDED && (
                <CustomButton
                  className={styles.voteButton}
                  secondary
                  disabled={!voteWithdrawable.gt(0)}
                  loading={voteWithdrawState.isLoading}
                  onClick={handleVoteWithdraw}>
                  Withdraw
                </CustomButton>
              )}
            </div>
          </div>
        </MainContainer>
      </div>
    </>
  );
};

export default VoteDetail;
