import React, { useEffect, useState, useMemo } from 'react';
import { Route, Switch, useRouteMatch, NavLink, Redirect } from 'react-router-dom';
import classnames from 'classnames';
import ProposalList from '../../components/Vote/ProposalList/ProposalList';
import styles from './Vote.module.scss';
import { useGetProposals } from '../../hooks/useVote';
import { PROPOSAL_STATE } from '../../utils/voteUtils';

const Vote = () => {
  const { path, url } = useRouteMatch();
  const { data: proposals } = useGetProposals();

  const reversedProposals = useMemo(() => proposals.slice().reverse(), [proposals]);
  const [filteredProposals, setFilteredProposals] = useState({
    [PROPOSAL_STATE.UPCOMING]: [],
    [PROPOSAL_STATE.LIVE]: [],
    [PROPOSAL_STATE.ENDED]: [],
  });

  useEffect(() => {
    const upcoming = reversedProposals.filter((item) => item.state === PROPOSAL_STATE.UPCOMING);
    const live = reversedProposals.filter((item) => item.state === PROPOSAL_STATE.LIVE);
    const ended = reversedProposals.filter((item) => item.state === PROPOSAL_STATE.ENDED);
    setFilteredProposals({
      [PROPOSAL_STATE.UPCOMING]: upcoming,
      [PROPOSAL_STATE.LIVE]: live,
      [PROPOSAL_STATE.ENDED]: ended,
    });
  }, [reversedProposals]);

  return (
    <div className={classnames(styles.Vote)}>
      <div className={styles.bannerWrap}>
        <div className={styles.banner}>
          <img className={styles.voteImg} src="/static/images/Vote/vote.png" alt="vote" />

          <div className={styles.titleWrap}>
            <div className={styles.titleInnerWrap}>
              <img className={styles.voteImgMobile} src="/static/images/Vote/vote.png" alt="vote" />
              <h1 className={styles.title}>Zebec Governance</h1>
            </div>

            <h2 className={styles.desc}>
              Join the ZBC community and shape the future of the protocol by participating in
              collective decision-making. Cast your vote or delegate to a trusted address on
              important upgrades and proposals.
            </h2>
          </div>

          <div className={styles.navLinkWrap}>
            <NavLink
              to={`${url}`}
              className={(isActive) =>
                classnames(styles.navLink, {
                  [styles.navLinkActive]: isActive,
                })
              }
              exact>
              <h2>All</h2>
            </NavLink>
            <NavLink
              to={`${url}/${[PROPOSAL_STATE.LIVE]}`}
              className={(isActive) =>
                classnames(styles.navLink, {
                  [styles.navLinkActive]: isActive,
                })
              }
              exact>
              <h2>{PROPOSAL_STATE.LIVE}</h2>
            </NavLink>
            <NavLink
              to={`${url}/${[PROPOSAL_STATE.ENDED]}`}
              className={(isActive) =>
                classnames(styles.navLink, {
                  [styles.navLinkActive]: isActive,
                })
              }
              exact>
              <h2>{PROPOSAL_STATE.ENDED}</h2>
            </NavLink>
          </div>
        </div>
      </div>
      <div className={styles.mainContent}>
        <Switch>
          <Route path={path} exact>
            <ProposalList items={reversedProposals} />
          </Route>
          <Route path={`${path}/${[PROPOSAL_STATE.LIVE]}`} exact>
            <ProposalList items={filteredProposals[PROPOSAL_STATE.LIVE]} />
          </Route>
          <Route path={`${path}/${[PROPOSAL_STATE.ENDED]}`} exact>
            <ProposalList items={filteredProposals[PROPOSAL_STATE.ENDED]} />
          </Route>
          <Redirect to={path} />
        </Switch>
      </div>
    </div>
  );
};
export default Vote;
