import { parseUnits } from 'ethers/lib/utils';
import Proposal0 from '../components/Vote/Proposals/Proposal0';
import { checkTime } from './utils';

export const PROPOSAL_INFO = {
  0: {
    DescComp: Proposal0,
    author: 'Zebec Community',
    quorum: parseUnits('732333103', 9).mul(33).div(100),
  },
  1: {
    DescComp: Proposal0,
  },
  2: {
    DescComp: Proposal0,
  },
};

export const PROPOSAL_STATE = {
  UPCOMING: 'upcoming',
  LIVE: 'live',
  ENDED: 'ended',
};

export const PROPOSAL_TYPE = {
  COMMUNITY: 0,
  CORE: 1,
};

export const OPTION_COLOR = ['#59D89D', '#E05608', '#f8db67', '#f0794b', '#8ac115'];

export const getProposalState = (startTime, endTime) => {
  if (!checkTime(startTime)) {
    return PROPOSAL_STATE.UPCOMING;
  }
  if (checkTime(endTime)) {
    return PROPOSAL_STATE.ENDED;
  }
  return PROPOSAL_STATE.LIVE;
};
